body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.container{font-size: 20px;padding:20px;}
table {width:400px;border-collapse:collapse;font-size:20px;}
table th{text-align: left;}
table tr{border-bottom:1px solid #eee;font-size:16px;}
table td:nth-child(1){width:100px;}
table td:nth-child(2){width:200px;}
table td:nth-child(3){width:100px;}
table td button{width:80px;background:crimson;color:#fff;border:none;border-radius:3px;}
table *{padding:5px 20px;}
form {padding:20px;box-sizing:border-box;width:400px;display:flex;flex-direction: column;}
form label {margin:5px;}
form input {padding:0 6px;height:30px;}
form input[type="button"] {margin-top:40px;width:100px;height:40px;border-radius:5px;background:steelblue;color:#fff;border:0}
